import { InjectionToken, Provider } from '@angular/core';
import { AccountState } from '@backbase/product-summary-common-ang';

export interface ProductSummaryAccountSelectorConfig {
  notToDisplayStateList: Array<string>;
  states: Array<AccountState>;
}

// These tokens can be used to change configuration at app level
export const BB_PRDSUM_ACCOUNT_SELECTOR_CONFIG_TOKEN: InjectionToken<ProductSummaryAccountSelectorConfig> = new InjectionToken(
  'BB_PRDSUM_ACCOUNT_SELECTOR_CONFIG_TOKEN',
);

export const productSummaryAccountSelectorDefaults: ProductSummaryAccountSelectorConfig = {
  notToDisplayStateList: ['Active', 'Open'],
  states: [
    {
      stateName: 'Active',
      severity: 'info',
    },
    {
      stateName: 'Inactive',
      severity: 'warning',
    },
    {
      stateName: 'Closed',
      severity: 'error',
    },
    {
      stateName: 'Open',
      severity: 'info',
    },
  ],
};

export const productSummaryAccountSelectorConfigProviders: Provider[] = [
  {
    provide: BB_PRDSUM_ACCOUNT_SELECTOR_CONFIG_TOKEN,
    useValue: productSummaryAccountSelectorDefaults,
  },
];
