import { Injectable, OnDestroy } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';

import { BehaviorSubject, Observable, of, combineLatest } from 'rxjs';

import {
  ProductSummaryHttpService,
  ProductSummary,
  GetProductSummaryRequestParams,
} from '@backbase/data-ang/arrangements';
import { Product, isProductVisible, ProductKind, cacheRequest } from '@backbase/product-summary-common-ang';

import {
  parseError,
  ProductKindsError,
  /**
   * these unused imports here because of issue in api-extractor
   * issue: https://github.com/microsoft/rushstack/issues/2140
   */
  /* eslint-disable @typescript-eslint/no-unused-vars,no-unused-vars */
  ProductKindsAccessDenied,
  ProductKindsBadRequest,
  ProductKindsUnknownError,
  /* eslint-enable */
} from './product-summary-error';
import { catchError, distinctUntilChanged, map } from 'rxjs/operators';

export type ListParams = any;

const defaultListParams: ListParams = {};

/**
 * Service for fetching and storing accounts and properties from model.xml
 *
 * This service relies on providers from `ProductSummaryAccountSelectorWidgetModule`.
 *
 * @see ProductSummaryAccountSelectorWidgetModule
 *
 * @usageNotes
 *
 * ### Ensure the ProductSummaryAccountSelectorWidgetModule is imported to your component module
 *
 * ```ts
 * @NgModule({
 *   ...
 *   imports: [
 *     ...
 *     ProductSummaryAccountSelectorWidgetModule,
 *   ],
 * })
 * export class MyWidgetModule {}
 * ```
 *
 * ### Inject this service into your component
 *
 * ```ts
 * @Component({
 *   ...
 *   providers: [ProductSummaryService],
 * })
 *  export class MyComponent {
 * ```
 */
@Injectable()
export class ProductSummaryService implements OnDestroy {
  /**
   * Constructor
   *
   * @param productSummaryDataService
   */
  constructor(private readonly productSummaryDataService: ProductSummaryHttpService) {}

  /**
   * Used to store the error occured during loading of accounts
   */
  readonly error = new BehaviorSubject<ProductKindsError | undefined>(undefined);

  private readonly listParams = new BehaviorSubject<ListParams>(defaultListParams);
  private readonly listRefresh = new BehaviorSubject(false);

  /**
   * List of accounts fetched from the server
   */
  readonly accountsList: Observable<Product[] | undefined> = combineLatest([
    this.listParams.pipe(distinctUntilChanged()),
    this.listRefresh,
  ]).pipe(cacheRequest(([params]) => this.getProductSummary(params)));

  /**
   * Refreshes the list of accounts
   */
  public refreshAccountsList() {
    this.listRefresh.next(true);
  }

  /**
   * @internal
   */
  ngOnDestroy() {
    this.error.complete();
  }

  private getProductSummary(params: GetProductSummaryRequestParams): Observable<Product[] | undefined> {
    return this.productSummaryDataService.getProductSummary(params).pipe(
      map((res: ProductSummary) => this.responseToProductList(res)),
      catchError((error: HttpErrorResponse) => {
        this.error.next(parseError(error));
        return of(undefined);
      }),
    );
  }

  private extractProducts(memo: Product[], { products }: { products: Product[] }): Product[] {
    return memo.concat(products);
  }

  private toProductKindList(res: ProductSummary): ProductKind[] {
    return Object.entries(res)
      .filter(([key]) => key !== 'aggregatedBalance')
      .map(([kind, value]) => {
        if (Array.isArray(value as ProductKind[])) {
          value.products = value.reduce(this.extractProducts, []);
        }
        let products = [];
        if (value.products && Array.isArray(value.products)) {
          products = value.products.filter(isProductVisible).map((product: Product) => ({ ...product, kind }));
        }
        return { products };
      });
  }

  private responseToProductList(res: ProductSummary): Product[] {
    return this.toProductKindList(res).reduce(this.extractProducts, []);
  }
}
