import { Component, Input } from '@angular/core';
import { AccountNumberType } from '@backbase/ui-ang/account-number-pipe';
import { ProductItemBasicAccountComponent } from '@backbase/ui-ang/product-item-basic-account';

/**
 * Displays account overview in account selector
 *
 * @usageNotes
 *
 * ### Display details
 *
 * ```html
 * <bb-product-summary-account-selector-item
 *   [title]="'My Account'"
 *   [amount]="12345"
 *   [currency]="'EUR'"
 *   [productNumber]="'12345678'"
 *   [productNumberType]="'iban'"
 * ></bb-product-summary-account-selector-item>
 * ```
 *
 * @ngModule ProductSummaryAccountSelectorWidgetModule
 */
@Component({
  selector: 'bb-product-summary-account-selector-item',
  templateUrl: './product-summary-account-selector-item.component.html',
})
export class ProductSummaryAccountSelectorItemComponent extends ProductItemBasicAccountComponent {
  /**
   * Whether to show in mobile or desktop view
   *
   * @default false
   */
  @Input() isCollapsedView = false;
  /**
   * Account number type
   *
   * @default undefined
   */
  @Input() productNumberType: AccountNumberType | undefined;
  /**
   * Whether to show external product information
   *
   * @default false
   */
  @Input() showExternalInfo = false;
  /**
   * Product external logo
   *
   * @default undefined
   */
  @Input() externalLogo: string | undefined;
  /**
   * Product external name
   *
   * @default undefined
   */
  @Input() externalName: string | undefined;
  /**
   * Product external sync date
   *
   * @default undefined
   */
  @Input() externalSyncDate: string | undefined;
}
