import { HttpErrorResponse } from '@angular/common/http';

export class ProductKindsBadRequest {
  messages: string[];
  kind: 'ProductKindsBadRequest' = 'ProductKindsBadRequest';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = ((response.error || {}).errors || []).map((e: any) => e.message);
  }
}

export class ProductKindsUnknownError {
  messages: string[];
  kind: 'ProductKindsUnknownError' = 'ProductKindsUnknownError';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = [response.message];
  }
}

export class ProductKindsAccessDenied {
  messages: string[];
  kind: 'ProductKindsAccessDenied' = 'ProductKindsAccessDenied';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = ((response.error || {}).errors || []).map((e: any) => e.message);
  }
}

export type ProductKindsError = ProductKindsAccessDenied | ProductKindsBadRequest | ProductKindsUnknownError;

export const parseError = (error: HttpErrorResponse): ProductKindsError => {
  if (error.error instanceof ErrorEvent) {
    return new ProductKindsUnknownError(error);
  } else {
    switch (error.status) {
      case 400:
        return new ProductKindsBadRequest(error);
      case 403:
        return new ProductKindsAccessDenied(error);
      case 401:
        return new ProductKindsAccessDenied(error);
      default:
        return new ProductKindsUnknownError(error);
    }
  }
};
